<template>
  <form @submit.prevent="AddFactura" >
            <div>
               
            <div class="mb-3">
                     <select 
                        name='cedula'
                        id="disabledSelect" 
                        class="form-select"  
                        v-model="factura.cedula"  
                        
                        >
                        <option>Selecciona el nombre</option>
                        <option
                            v-for="soc in getSociosAll"
                            :key="soc._id"  
                            :value="soc.cedula"                      
                        >   
                            {{soc.nombre}} {{soc.apellidos}}
                        </option>
                    </select>
                </div>

               
                 <input
                    type='date'
                    name='fecha'
                    class='form-control mt-3'
                    placeholder='Fecha'
                    autoComplete='off'
                    v-model="factura.fecha"
                /> 
            
                
                <input
                    type='text'
                    name='descripcion'
                    class='form-control mt-3'
                    placeholder='Descripcion'
                    autoComplete='off'
                    v-model="factura.descripcion"

                />
                 <input
                    type='number'
                    name='monto'
                    class='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    v-model="factura.monto"

                />
                

                <button 
                class='btn btn-primary form-control  mt-2'
                type='submit'  
                :disabled="getIsLoading"
                >
                Guardar

                </button>

            </div>
        </form>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    data() {
        return{
            factura:{}
        }
    },
    computed:{
        ...mapGetters('socio',['getSociosAll','getIsLoading'])
    },
    methods:{
        ...mapActions('socio',['getSocios']),
        ...mapActions('facturar',['addFactura']),
        async AddFactura(){
            var facturaAdd = {
                cedula: this.factura.cedula,
                fecha: this.factura.fecha,
                descripcion: this.factura.descripcion,
                monto: this.factura.monto
            }
            await this.addFactura(facturaAdd)
        }
    },
    created(){
        this.getSocios()
    }
}
</script>

